.create-controls {
    display: flex;
    flex-direction: column;
    border-top: rgb(140, 140, 140, 0.5) solid 1px;
    border-bottom: rgb(140, 140, 140, 0.5) solid 1px;
    padding: 2rem 1rem;
    margin-bottom: 7rem;
    align-items: center;
}

.create-contorls-name-input {
    width: 80%;
}

.create-controls-locations-container {
    display: flex;
    flex-direction: column;
    margin: 1rem 0;
}

.create-controls-locations-container > p {
    font-size: 1.5rem;
    margin-bottom: 10px;
    font-weight: bold;
    text-align: center;
    text-transform: uppercase;
}

.create-controls-radio {
    display: flex;
    align-items: center;
}

.create-controls-container {
    display: flex;
    align-items: center;
    justify-content: center;
    align-content: center;
    margin-bottom: 2rem;
}

.create-controls-container > p {
    margin: 1rem;
    font-weight: bold;
    text-transform: uppercase;
    font-size: 1.2rem;
}